                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户后续进货以在线下单的方式提交,</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">收到用户的订单及相应货款后,按照各经销商订单顺序进行备货并发货。对批量较大需求紧急的订货,用户应提前通知</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">以便</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">及时组织生产并供货。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">3.3</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">货物由用户自提,或者按照用户确认的收货信息由</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">代办托运,运费根据</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">的相关规定由用户自行承担。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 12.75pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 12.75pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">3.4</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户签收货物前务必开箱验货,签收前已经发生的货损、货差等问题,经确认属于</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">责任的,由</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">按照最小计量单位“袋/瓶/</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">碗/套/</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">盒”进行一比一换货。如因运输过程中的原因,导致货物毁损、</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">丢失的,用户向承运人主张赔偿责任。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:3.75pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <h1 style="background-color:#ffffff; line-height:18pt; margin:15pt 0pt 7.5pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">第四条</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">退换货及售后</span></h1>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">4.1</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">在产品保质期内,经</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">确认存在质量问题的货物,</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">按照最小计量单位“袋/瓶/碗/套/盒”进行一比一退换货,因产品质量问题导致退换货的运费由</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">承担。除该情形外,</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">产品一经售出，</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-1.5pt; text-transform:none">超过</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-1.5pt; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">7</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">天后，非因质量问题不退不换。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">4.2</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户在经营</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">产品时,应本着先进先出原则,因用户经营或者管理不善而导致产品临期或者过期的,</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">概不承担任何退换货责任。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:1.5pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">4.3售后责任:</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:0.75pt 0pt 3.75pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 29.25pt 0pt 6pt; text-indent:27.75pt"><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">(1)</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户是所售货物的第一售后责任人,须按</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">售后服务规定及时、尽责为顾客提供售后服务,并做记录。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 6pt; text-indent:27.75pt"><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">(2)</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户若接获客户任何对于本协议商品与服务相关的抱怨、争议或投诉,应及时妥善处理,并将相关信息以书面形式通知</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 12.75pt 0pt 6pt; text-indent:27.75pt"><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">(3)</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">如客户提出产品有质量问题要求退换,经查实确属产品质量
                问题,用户应及时向</span><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">报告情况,并在</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">的指导下先予客户退换</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">或补偿。用户处理或补偿后依据处理或补偿结果协助</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">向厂家追偿,</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">但若产品质量问题非</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">或厂家原因造成则由用户自行承担责任,不得影响品牌及公司形象。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">因用户怠于处理售后致使客户投诉至</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">或其他行政主管部</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">门,</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">有权直接处理,产生的费用由用户承担。造成不良后果和负面</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">影响的,</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">有权解除本协议、取消用户经销资格并要求赔偿全部损失。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <h1 style="background-color:#ffffff; line-height:18pt; margin:15pt 0pt 7.5pt; text-align:justify"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">第五条</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">用户的权力义务</span></h1>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:0pt 0pt 3.75pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 29.25pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户向</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">承诺在本协议授权期限内均遵守并履行以下条款：</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 29.25pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">5.1</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">积极宣传和维护</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">产品的形象提供有效的服务,拥有稳定的客户群体并不断壮大</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 29.25pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 29.25pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">5.2</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">努力开发潜力市场及时反馈市场和产品相关信息:
                在推广和销售活中遵纪守法，对双方非公开信息负有保密义务。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 29.25pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:1.5pt 29.25pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">5.3</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户应合理制定销售计划,积极解决市场问题,保证销售计划的实现及市场的持续发展,管理好各级经销商,服务好各类客户。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:1.5pt 29.25pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 19.5pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">5.4</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">本协议有效期内,</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">应及时向用户提供最新的产品资料,向用户提供必要的产品、技术培训,帮助用户迅速掌握产品性能和销售</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-0.75pt; text-transform:none">技巧。依据业务推广计划,对用户开展的市场推广活动予以支持,并有</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">权对用户市场销售和推广活动的具体实施进行监督、检查。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 12.75pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 12.75pt 0pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">5.5未经</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">事前书面授权许可,用户不得擅自使用也不得允许他人使用包括但不限于企业名称</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-3pt; text-transform:none">、</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">字号</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-3pt; text-transform:none">、</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">商标</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; letter-spacing:-3pt; text-transform:none">、</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">专利等</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">的所有知识产权,否则视为用户违约,用户应向</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">缴纳其违法所得收益,且</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">有权要求用户支付五万元违约金,违约金不能弥补</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">因此而遭受的损失的,用户应继续赔偿。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 0pt; text-align:justify"><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 0pt; text-align:justify"><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">5.6</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户不得经营和销售过期产品,经营和销售临期产品时应遵守国家相关法律法规,否则,因此导致的相关纠纷及执法机关查处,由用户自行处理,并赔偿</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">为处理用户该行为导致的后果和消除该行为不良影响而造成的全部损失。</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 0pt; text-align:justify"><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; line-height:20.75pt; margin:0pt 22.5pt 0pt 0pt; text-align:justify"><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">5.7</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户应保证具备经销产品要求的经营资质,确保自身经营、销售、推广宣传等行为的合法性,因用户不具备相关资质或者不合规行为给</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">造成损失或使</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">参与到诉讼、仲裁等法律行为中的,用户应赔偿</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">受到的全部损失并承担该法律行为的全部后果。(注：代理享受公司营业额10%的宣传费，直至总计分红额返完为止。如不宣传，公司有权收回返利，最终解释权归烟台耀远生物科技有限公司所有。)</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">5.8</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">用户有权为经销范围内的产品拓展下级经销商,但不得采用任何国家法律法规明确禁止的销售方式进行拓展和销售。用户应按照本协议及</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">相关管理制度要求规范下级经销商的经营行为,并对其自</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">行拓展的下级经销商的经营行为承担法律责任。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">5.9本协议系甲乙双方自愿协商缔结,用户因销售以及推广本协议约定产品而投入的资金、办公场所、销售和行政人员、仓库等均属于用户经营性成本,由用户自行承担。本协议因任何原因而终止,用户均无权要求</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">对前述投入成本予以赔偿。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">第六条</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">经营限制</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">6.1用户经营的项目原则上以与</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">的合作项目为限,用户与</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">合作前既有或经</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">同意的其他项目不在此限。用户与</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">合作前既有项目,用户需如实向</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">披露相关情况。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">6.2合作期限内以及合作终止后,禁止用户开发/经营与经销产品属于同类的产品,自营、参与经营与</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">公司有竞争关系的业务,不得对</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">经销商存在诱导经销其他品牌产品等“挖角”行为,不得捏造并公开散布任何有损于</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">公司或品牌商业信誉和产品声誉的言论
                和信息。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">6.3合作期限内,用户经营的非同类产品禁止使用与本协议经销产品相同的经营模式,不得与经销产品销售相冲突(包括但不限于以其他产品与</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">的产品组合销售、诱导</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">其他经销商经销其他品牌产品等行为)。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">6.4用户如违反前述规定,除赔偿因此给</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">造成的损失外,还应向</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">支付违约金五万元,</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">有权解除本协议,取消用户经销资格。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">6.5用户凡低于</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">产品</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">零售价出售，均视为乱价，公司给予停止宣传奖处理。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="color:#333333; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">6.6各级别代理商需在新品上市购买定量产品。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">第七条</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">保密条款</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">7.1
                在合作过程中用户应严格保守所知悉的</span><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">的商业秘密,包括但不限于未经披露的技术信息、经营模式、客户信息、交易账目等,
                承诺不得以任何方式公开、泄露或者为自身利益使用该商业秘密,并确保不会将该信息用于执行或履行其在本协议中的权利或义务之外的其他目的。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">7.2
                用户违反本保密条款,</span><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">有权解除本协议,并要求用户支付违约金五万元;违约金不足以弥补</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">损失的,</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">有权从履约保证金或者应付用户的其他费用中扣除。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">7.3
                本保密条款永久有效,不因本协议的解除、终止而终止。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">第八条</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">协议终止与解除</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">有以下情形的,</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">有权在任何时候通知用户单方解除本协议,取消用户经销资格,协议自通知送达之日起解除:</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">8.1
                用户的经营行为或者其他行为存在严重扰乱</span><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">产品销售市场,或者导致/可能导致</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">品牌形象遭受负面影响的。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">8.2
                用户或者用户拓展的下级经销商在经营过程中存在违法违规、违反</span><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">制度或者损坏</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">利益的行为,用户未能及时纠正的。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">8.3
                用户第一负责人更换、公司解散或营业终止、与他人合并、主要股东变更或管理层重大变动等足以影响双方之间的信赖、合作关系稳定发展的。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">8.4
                用户第一负责人或法定代表人受到刑事处罚的。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">8.5
                未经</span><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">事先书面同意,用户将本协议项下的债权、债务或权利、义务转让给任何第三方.</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">8.6
                用户存在其他违约行为,经</span><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">告知后没有在</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">指定的期限内进行纠正的。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">8.7
                用户违反约定,在本协议或经销商授权因任何原因终止或者解除后,继续销售</span><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">产品,或将</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">的产品/品牌作为自身商号、产品标识继续使用的,除停止销售和使用行为外,还应当向</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">肯木咖啡</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">支付</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">相应的</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">违约金</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">第九条</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">不可抗力条款</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">如因不可抗力(包括但不限于火灾、爆炸、洪水、地震、台风、
                自然灾害、叛乱、暴动、或国家法律法规、政策变动等双方都无法预见、双方都无法合理控制的原因)致本协议不能履行或不能全部履行时,遭遇不可抗力事件的一方或双方均可部分或全部免责。但应在不可抗力事件结束后 10
                天内向对方说明理由并提供相关的证明文件。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">第十条</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span><span
                style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">争议解决</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; font-weight:bold; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">10.1
                因履行本协议发生争议的,由甲乙双方协商解决。协商不能达成一致的,任何一方均可以向仲裁委员会申请仲裁裁决。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">10.2
                违约方应承担守约方为主张合法权益所支出的合理费用,包括但不限于律师费、仲裁费、公证费、保全费、鉴定费等。</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">&#xa0;</span></p>
        <p style="background-color:#ffffff; font-size:10.5pt; line-height:115%; margin:3.75pt 0pt"><span style="background-color:#ffffff; font-family:宋体; font-size:10.5pt; font-style:normal; text-transform:none">10.3
                本协议确定的双方地址、联系人和通信终端,为具有法律效力的送达方式。双方确认:双方在协议中填写通讯地址是各方的送达地址,双方以本协议中联系地址送达文件等,均为有效送达。若一方提供的地址虚假、或变更联系地址而不书面通知相对方,若发生送达不到时,由提供虚假地址或变更联系地址方承担送达不能的法律后果。</span></p>
    </div>
</template>

<script>
    export default {
        name: "UserBook"
    }
</script>

<style scoped>

</style>



